.header-wrapper{
  background-color: #EBE5E5;
  width: 100%;
  -webkit-box-shadow: 1px 1px 15px #a7a7a7;
          box-shadow: 1px 1px 15px #a7a7a7;
  margin-right: 5%;
  padding-right: 30px;
  font-size: 1.5rem;
}
.header-wrapper ul{
  list-style-type: none;
  margin: 0;
  float: right;
}
.header-wrapper a{
  float: left;
  cursor: pointer;
  clear: right;
  padding: 20px;
  text-decoration: none;
  color: black;
}
.header-wrapper a:hover{
  color: grey;
}



